export default {
	props: {
		show: Boolean
	},
	methods: {
		// 操作菜单
		addfriend () {
			this.$emit('addfriend');
		},
		// 清除事件
		clear () {
			this.$emit('clear');
		},
		// 隐藏popup
		hidepopup () {
			this.$emit('hidepopup');
		},
		// 显示popup
		showpopup () {
			this.$emit('showpopup');
		}
	}
}
